.home-root {
  font-size: 12px;
  background-color: #282c34;
  color: #ffffff;
  min-height: 100vh;
}

.home-header {
  margin: 0;
  letter-spacing: 0.01em;
  background-color: #20232a;
  min-height: 50px;
  display: flex;
  align-items: center;
  padding: 0 20px;
  flex-direction: row;
  flex-wrap: wrap;
}

.home-header .logo {
  font-weight: 700;
  font-size: 16px;
  color: #61dafb;
}

.home-header ul {
  flex: 1;
  list-style-type: none;
  margin: 0;
  padding: 0 20px;
  height: 100%;
}

.home-header ul .nav {
  float: left;
  position: relative;
  color: white;
  text-align: center;
  padding-left: 10px;
  padding-right: 10px;
  text-decoration: none;
  min-height: 50px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
}

.home-header ul .nav:hover {
  background-color: #111111;
}

.home-header ul .nav.active {
  color: rgb(97, 218, 251);
}

.home-header ul .nav.active span {
  position: absolute;
  bottom: -1px;
  height: 4px;
  background: rgb(97, 218, 251);
  left: 0px;
  right: 0px;
  z-index: 1;
}

.home-header .version {
  color: inherit;
  text-decoration: none;
}

.home-body {
  padding: 20px;
  position: relative;
}

.home-body a {
  position: absolute;
  top: 5px;
  right: 20px;
  color: #e3e3e3;
  text-decoration: none;
}